
.menuContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	margin-left: auto;
	margin-right: auto;
	background-color: transparent;
	height: 2%;
	width: 90%;
	font-size: 80%;
	z-index: 100;
	padding: 0;
	cursor: pointer;
}

.nav-item{
	display: -ms-flexbox;
	display: flex;
	min-width: 205px;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 25px;
	border: solid 1px #ffffff;
	background-color: rgb(220, 226, 132);
}

#menuReduzido{
	-ms-flex-line-pack: center;
	    align-content: center;
	-ms-flex: 1 1;
	    flex: 1 1;
}

.nav li a{
	display: -ms-flexbox;
	display: flex;
	padding: 3px 25px;
	color: #000;
}
.nav .open a{
	background-color: #77A20E;
}
.nav .open a:hover{
	color: #000;
	font-weight: bold;
}
.dropdown li a:hover{
	color: #fff;
	background-color: #77A20E;
}
.dropdown li{
	background-color: #6A910C;
	padding: 0px 5px;
	
} 
.dropdown-menu{
	font-size: 0.8rem;
}


  









